.card-container {
  flex-direction: column;
  gap: 20px;
  padding: 20px 15px;
  display: flex;
}

.card-container .card {
  width: 200px;
  height: 50px;
  text-align: center;
  opacity: 0;
  pointer-events: none;
  background-color: #f2cbbd;
  border: 2px dashed #000;
  border-radius: 10px;
  transition: all .15s;
  transform: translateX(100px);
}

.card.show {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

